<template>
  <div class="box allCenter"
    v-show="tipsData.text">
    <div :class="[{successColor:tipsData.bl},{failureColor:!tipsData.bl}]">
      <i class="icon biFont icon-success success"
        v-if="tipsData.bl"></i>
      <i v-else
        class="icon biFont icon-failure failure"></i>
      <span v-html="tipsData.text"></span>
    </div>
  </div>
</template>
<script>
//使用规则
// this.$store.dispatch('publicTools/setTipsData', [Btring, Boolean,Number]);
//传参必须为数组
//数组中第一个参数为展示的文字-------必传
//数组中第二个参数为样式的类型-------可选--------默认值为false
//数组中第一个参数为展示的时间-------可选--------默认为1500
import { mapGetters } from 'vuex';
export default {
	computed: {
		...mapGetters('publicTools', {
			tipsData: 'getTipsData'
		})
	}
};
</script>
