<template>
  <div class="containter"
    @mouseenter="cancelFileClear"
    @mouseleave="startFileClear"
    v-show="fileList.length">
    <header>
      <span class="unSelect">任务状态</span>
      <span class="biFont icon-clear task-clear"
        @click="clearTask"></span>
    </header>
    <!-- style="height:calc((1vw + 0.625vw) * 5)" -->
    <div :style="{height:fileList.length>5?'8.5vw':''}">

      <easy-scroll :barOption="opt"
        :viewStyle="viewStyle">
        <ul class="fileList">
          <li v-for="(item,index)  in fileList"
            :key="index"
            class="unSelect">
            <span>{{item.source_name}}</span>
            <span>{{item.status==1?'上传完成':'上传失败'}}</span>
          </li>
        </ul>
      </easy-scroll>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
	name: 'fileUploadList',
	data() {
		return {
			/**滚动条 */
			opt: {
				barColor: '#95A0B6', //滚动条颜色
				railColor: '#475566', //导轨颜色
				barWidth: 5, //滚动条宽度
				barMarginRight: 6, //垂直滚动条距离整个容器右侧距离单位（px）
				barOpacityMin: 0.3, //滚动条非激活状态下的透明度

				autoHideMode: true //自动隐藏模式
			},
			viewStyle: {
				padding: '0 1vw'
			},
			timerId: {}
		};
	},
	computed: {
		...mapGetters('main', {
			fileList: 'getFileUploadList',
			isFileClear: 'isFileClear'
		})
	},
	methods: {
		/**获取推送的消息 */
		getPushMessage() {
			let vm = this;
			vm.$get('/api/pushMessage')
				.then(res => {
					// if (res.code == '000000') {
					// }
				})
				.catch(error => console.error(error));
		},
		startFileClear() {
			this.$store.commit('main/toggleFileClear', true);
		},
		cancelFileClear() {
			this.$store.commit('main/toggleFileClear', false);
		},
		/**清除任务 */
		clearTask() {
			let vm = this;
			let sourceList = [];
			vm.fileList.forEach(e => {
				const source_cn = {
					source_sn: e.source_sn
				};
				sourceList.push(source_cn);
			});
			let sourceListJson = JSON.stringify(sourceList);
			vm.$put('/api/receiveMessage', { sourceList: sourceListJson })
				.then(res => {
					vm.$store.commit('main/clearFileUploadList');
				})
				.catch(error => console.error(error));
		}
	},
	mounted() {
/* 		this.getPushMessage(); */
	},
	watch: {
		isFileClear: {
			immediate: true,
			handler(val) {
				if (val) {
					if (this.fileList.length) {
						clearInterval(this.timerId);
						this.timerId = setTimeout(() => {
							this.clearTask();
						}, 1000 * 10);
					}
				} else {
					clearTimeout(this.timerId);
				}
			}
		},

		'fileList.length'(val) {
			if (!val) {
				this.cancelFileClear();
			}
		}
	}
};
</script>

<style  scoped>
.containter {
  position: absolute;
  z-index: 1000;
  bottom: 0.5vw;
  right: 0.5vw;
  width: 16.7vw;
  background-color: #1e2738;
  box-shadow: 0vw 0.3vw 0.6vw 0vw rgba(0, 0, 0, 0.6);
}
header {
  width: 16.7vw;
  height: 1.9vw;
  background-color: #2d374f;
  position: relative;
}

header span {
  display: block;
  height: 1.9vw;
  line-height: 1.9vw;
  text-align: center;
  color: #ffffff;
}
header span:first-child {
  font-size: 0.8vw;
}
header .task-clear {
  cursor: pointer;
  font-size: 0.6vw;
  position: absolute;
  right: 0.6vw;
  top: 0;
  margin-top: 0.1vw;
}
header .task-clear:hover {
  color: #28b3fc;
}

ul.fileList {
  padding: 0.5vw 0.5vw 0.2vw 0.5vw;
}
ul.fileList > li {
  line-height: 1vw;
  border-bottom: solid 0.0625vw #171e2b;
  padding: 0.25vw 0.6vw;
}
ul.fileList > li:last-child {
  border: none;
}
ul.fileList > li span:first-child {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 8.5vw;
}
ul.fileList > li span {
  color: #ffffff;
  font-size: 0.7vw;
}
ul.fileList > li span:nth-child(2) {
  float: right;
}
</style>
