<template>
  <header>
    <router-link tag="div"
      class="logo"
      to="main">
      <img class="allCenter"
        src="~@/assets/Header/img/24e-logo.svg"/>
    </router-link>
    <div class="navBar">
      <ul class="nav">
        <li
          v-for="(item,index) in navBarBtnList"
          :key="index"
          :class="{active:item.activeName.indexOf($route.name)>-1}"
          class="choice visualization"
        >
          <router-link :to="{name:item.routerName}">
            <span class="biFont"
              :class="'icon-'+item.className"></span>
          </router-link>
          <span class="tips aCenter">{{item.title}}</span>
        </li>
      </ul>
      <ul class="btn-wrapper">
<!--        <li class="lang"-->
<!--          :class="{active:activeBtn==1}"-->
<!--          @click="btnActiveToggle(1)">-->
<!--          <router-link to="./lang">-->
<!--            <span class="biFont icon-lang"></span>-->
<!--          </router-link>-->
<!--        </li>-->
<!--        <li class="theme"-->
<!--          :class="{active:activeBtn==2}"-->
<!--          @click="btnActiveToggle(2)">-->
<!--          <router-link to="./skin">-->
<!--            <span class="biFont icon-skinBtn"></span>-->
<!--          </router-link>-->
<!--        </li>-->
<!--        <li class="news"-->
<!--          :class="{active:activeBtn==3}"-->
<!--          @click="btnActiveToggle(3)">-->
<!--          <router-link to="./news">-->
<!--            <span class="biFont icon-announcement"></span>-->
<!--          </router-link>-->
<!--        </li>-->
      </ul>
    </div>
    <router-link :to="{name:'userCenter'}">
      <div class="userInfo">
        <div class="triggerHover"></div>
        <div class="avatar-wrapper aCenter">
          <div class="avatar vCenter">
            <div class="rotateLeft">
              <div class="rotateRight">
                <div class="rotateCenter"
                  :style="`background-image:url(${userInfo.iconUrl});`"></div>
              </div>
            </div>
          </div>
        </div>
        <div class="user-info-dropdown">
          <ul>
            <li>
              <div class="userName">{{userInfo.nickName}}</div>
            </li>
            <li v-for="(info,index) in userInfoList"
              :key="index"
              @click="hideDropdown(info.text)">
              <router-link :to="info.href">
                <span class="userCenterFont icon"
                  :class="info.iconName"></span>
                <span class="text">{{info.text}}</span>
                <span class="surplus vCenter"
                  v-if="info.accountBtn">￥{{balance.topupMoneySum}}</span>
                <button class="recharge vCenter"
                  v-if="info.accountBtn">{{info.accountBtn}}
                </button>
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </router-link>
  </header>
</template>
<script>
import {mapGetters} from 'vuex';
import '@/assets/UserCenter/fonts/iconfont.css';
import { getCookie} from '@/utils/utilsMethods';
export default {
	name: 'bi-header',
	data() {
		return {
			show: 1,
			activeBtn: -1,
			navBarBtnList: [
				{
					className: 'visualization',
					activeName: ['visualization', 'screen'],
					routerName: 'visualization',
					title: '可视化'
				},
				{
					className: 'dataSource',
					activeName: ['myDataSource'],
					routerName: 'myDataSource',
					title: '数据源'
				},
				{
					className: 'dataModeling',
					activeName: ['dataModeling'],
					routerName: 'dataModeling',
					title: '数据建模'
				},
				{
					className: 'case',
					activeName: ['cases'],
					routerName: 'cases',
					title: '我的案例'
				}
			],
			userInfoList: [
				{
					iconName: 'icon-basicInfo',
					text: '基本资料',
					href: '/basicInfo'
				},
				{
					iconName: 'icon-securitySetting',
					text: '安全设置',
					href: '/securitySetting'
				},
				{
					iconName: 'icon-certification',
					text: '实名认证',
					href: '/certification'
				},
				{
					iconName: 'icon-accountBalance',
					text: '账户余额',
					href: '/accountOverview',
					textName: '800元',
					accountBtn: '充值'
				},
				{
					iconName: 'icon-vip',
					text: '开通会员',
					href: '/renew'
				},
				{
					iconName: 'icon-consumingRecords',
					text: '消费记录',
					href: '/userCenter/accountOverview/ExpensesRecord'
				},
				{
					iconName: 'icon-renew',
					text: '产品续费',
					href: '/accountOverview'
				},
				{
					iconName: 'icon-quit',
					text: '退出',
					href: '#'
				}
			]
		};
	},
	methods: {
		active: function (index) {
			this.show = index;
		},
		btnActiveToggle: function (index) {
			this.showSkin = index;
		},
		hideDropdown(i) {
			if (i === '退出') {
				let token=getCookie('token');
				// let callbackUrlPathname = window.location.pathname;
				// let callbackUrlHost = window.location.host;
				// let url = callbackUrlPathname
				// 	? callbackUrlHost + callbackUrlPathname
				// 	: callbackUrlHost;

				this.$post('api/logout', {
					token,
					loginType:'pc',
					callback: window.location.origin +'/main'
				}).then(res => {
					if (res.code == '000000') {
						let exdate = new Date();
						exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * -1);
						document.cookie = 'token=\'\'' + ';expires=' + exdate.toGMTString() + ';domain=.24e.co;path=/';
						window.location.href = this.ssoUrl+window.location.origin +'/main';
					}
				});
			}
		},
	},
	computed: {
		...mapGetters('userCenter', {
			userInfo: 'getUserInfo',
			balance: 'getBalance'
		}),
	  ...mapGetters('config',
		  ['ssoUrl']
	  )
	}
};
</script>

<style scoped>
  header {
    width: 100%;
    height: 2.6vw;
    line-height: 2.6vw;
    background-color: #252f40;
  }

  header > div {
    float: left;
  }

  header .logo {
    float: left;
    position: relative;
    border-right: 0.0625vw solid #020311;
    width: 2.6vw;
    height: 2.6vw;
    cursor: pointer;
  }

  header .logo img {
    width: 1.2vw;
    height: 1.2vw;
    vertical-align: middle;
  }

  .surplus {
    padding-left: 0.2vw;
    font-size: 0.7vw;
    color: #3dadff;
  }

  .user-info-dropdown .recharge {
    cursor: pointer;
    padding: 0 0.2vw;
    width: auto;
    white-space: nowrap;
    height: 1.1vw;
    display: inline-block;
    font-size: 0.7vw;
    color: #ffffff;
    background-color: #28b3fc;
    right: 1.5vw;
    line-height: 1.1vw;
  }

  .navBar {
    /* width: 88.3vw; */
    width: 92.9vw;
    height: 100%;
    border-right: 0.06vw solid #020311;
  }

  .navBar .nav {
    float: left;
    height: 100%;
  }

  .navBar .nav li {
    float: left;
    width: 3.2vw;
    height: 100%;
    text-align: center;
    cursor: pointer;
    position: relative;
  }

  .navBar .nav li:hover .tips {
    visibility: visible;
  }

  .navBar .nav .tips {
    visibility: hidden;
    display: inline-block;
    position: absolute;
    font-size: 0.6vw;
    padding: 0 0.4vw;
    color: #ffffff;
    text-align: center;
    width: auto;
    height: 1.1vw;
    line-height: 1.1vw;
    white-space: nowrap;
    top: calc(100% + 0.1vw);
  }

  .navBar .nav li.active::after {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    content: " ";
    width: 100%;
    height: 2px;
    background: #3dadff;
  }

  .navBar a {
    display: block;
    width: 3.2vw;
    height: 100%;
    text-decoration: none;
    color: #fff;
  }

  .navBar .nav li.active a span,
  .navBar a:hover span {
    color: #3dadff;
  }

  .navBar span {
    line-height: 2.7vw;
    font-size: 1vw;
  }

  .btn-wrapper {
    width: 7.5vw;
    height: 100%;
    float: right;
  }

  .btn-wrapper li {
    float: left;
    width: 2.3vw;
    height: 2.5vw;
    text-align: center;
    cursor: pointer;
  }

  .btn-wrapper li a {
    pointer-events: none;
    width: 2.3vw;
    height: 2.6vw;
    text-decoration: none;
    color: gray;
  }

  .btn-wrapper li span {
    line-height: 2.7vw;
    font-size: 1vw;
  }

  /*.navBar .btn-wrapper li:hover span ,*/
  .navBar .btn-wrapper li.active span {
    color: #3dadff;
  }

  .userInfo {
    cursor: pointer;
    float: left;
    position: relative;
    width: 4.5vw;
    height: 100%;
  }

  .userInfo .triggerHover {
    z-index: 3;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2.175vw;
    background-color: transparent;
  }

  .userInfo .avatar-wrapper {
    /* padding: 0 0.3vw 0 1vw; */
    height: 100%;
  }

  .userInfo .avatar {
    position: relative;
    overflow: hidden;
  }

  .userInfo .avatar .rotateLeft {
    width: 1.4vw;
    height: 1.75vw;
    transform: rotate(120deg);
    overflow: hidden;
  }

  .userInfo .avatar .rotateRight {
    width: 1.4vw;
    height: 1.75vw;
    transform: rotate(-60deg);
    overflow: hidden;
  }

  .userInfo .avatar .rotateCenter {
    background: #fff;
    width: 1.4vw;
    height: 1.75vw;
    transform: rotate(-60deg);
    background-size: 125% auto;
    background-repeat: no-repeat;
    background-position: center;
  }

  .userInfo .userName {
    width: 100%;
    height: 100%;
    color: #fff;
    font-size: 0.7vw;
    display: inline-block;
  }

  .userInfo .user-info-dropdown {
    pointer-events: none;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 2.6vw;
    width: 14.1vw;
    height: auto;
    z-index: 99999;
    background-color: #1e2533;
    transition: opacity 500ms ease;
  }

  .userInfo .user-info-dropdown ul {
    width: 100%;
    height: 100%;
  }

  .userInfo .triggerHover:hover {
    height: 100%;
  }

  .userInfo .triggerHover:hover ~ .user-info-dropdown {
    pointer-events: auto;
    opacity: 1;
    transition: opacity 500ms ease;
  }

  .user-info-dropdown:hover {
    pointer-events: auto;
    opacity: 1;
  }

  .userInfo .user-info-dropdown ul li {
    padding-left: 1vw;
    width: 100%;
    height: 1.8vw;
    line-height: 1.8vw;
  }

  .userInfo .user-info-dropdown ul li:last-child {
    border-top: 0.0625vw solid #131721;
  }

  .userInfo .user-info-dropdown ul li:hover {
    background-color: #131721;
    color: #3dadff;
  }

  .userInfo .user-info-dropdown ul li:hover a {
    color: #3dadff;
  }

  .userInfo .user-info-dropdown ul li a {
    display: block;
    width: 100%;
    height: 100%;
    color: #fff;
    position: relative;
  }

  .userInfo .user-info-dropdown ul li a span.icon {
    font-size: 0.7vw;
    margin-right: 0.1vw;
  }

  .userInfo .user-info-dropdown ul li a span.text {
    font-size: 0.7vw;
    vertical-align: middle;
  }

  .biFont:hover {
    color: #3dadff;
  }

  .navBar .btn-wrapper li.active span[data-v-253828fb] {
    color: #fff;
  }

  .navBar .btn-wrapper li.active span[data-v-253828fb]:hover {
    color: #3dadff;
  }
</style>
